<template>
  <div class="maxbox">
      <img src="./images/designtitle.svg" class="title">
      <div class="hengxian"></div>
      <router-view></router-view>
      <div class="list">
        <a href="javascript:;" :class="{noactive:l.focus == 0 , active:l.focus == 1}" v-for="l in list" :key="l.id" @click="qiehuan(l,list)">{{ l.text }}</a>
      </div>
      <a class="tohome" href="javascript:;" @click="toHome">
        <div class="jiantou">
          <div class="jiantou1"></div>
          <div class="hengxian"></div>
        </div>
        <span>To Home</span>
      </a>
  </div>
</template>

<script>
export default {
  name:'socialize',
  data(){
    return {
      list:[
        {id:1,text:'Socialize',focus:1,name:'socialize'},
        {id:2,text:'E-commerce',focus:0,name:'commerce'},
        {id:3,text:'Visualization',focus:0,name:'visualization'},
        {id:4,text:'Other types',focus:0,name:'othertypes'},
        {id:5,text:'AI Research',focus:0,name:'airesearch'},
      ]
    }
  },
  methods:{
    //点击切换按钮样式并跳转路由
    qiehuan(item,items){
      //把所有focus改为0
      items.forEach(element => {
        element.focus = 0;
      });
      //当前所点击的focus改为1
      item.focus = 1;
      
      //跳转到所点击的按钮路由
      //如果再次点击当前页面按钮切换路由，则不处理任何，否则跳转到对应路由
      if(item.name !== this.$route.name){
        this.$router.push({
          name:item.name
        })
      }else{
        return
      }

    },
    //跳转到home路由
    toHome(){
      this.$router.push({
        name:'home'
      })
    }
  }
}
</script>

